* {
  box-sizing: border-box;
}
body{
  background-color: #F8F8FF;
}
.header-section{
  margin:auto;
  max-width:1200px;
  text-align:center;
  background-color:black;
  color:white;
  min-height:50px;
  display:flex;
  flex-direction:column;
  justify-content:center;
  font-family:dana VF
}

.carausal-container{
  min-height:400px;
  max-width:1200px;
  margin:auto;
  display:flex;
  flex-direction:column;
  justify-content:center;
  background-color:#E0FFFF
}

.outer-container{
  margin:auto;
  max-width:1200px;
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  justify-content:center;
  background-color:#FFFAF0
  

}
.inner-container{
  background-color:#F8F8FF;
  width:250px;
  margin:5px;
  display:flex;
  flex-direction:column;
  border-radius:10px;
  border: 1px solid burlywood;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
  
}
.inner-container:hover { 
  -webkit-transform: scale(1.5); 
  transform: scale(0.9); 
}

.image-container{
  max-width:100%;
  height:200px
}

img{
  width:100%;
  height:200px;
  border-top-left-radius:10px;
  border-top-right-radius: 10px;
}

.details-container{
  justify-content:space-between;
}
.details-container h6{
  text-align:center;
}
.details-container p{
  font-size:12px;
  margin:0px 3px
}

a:hover{
  color: white;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.load-container{
  height:100vh;
  max-width:1200px;

}
.media-link{
  text-decoration:none;
  
}
#media-container{
  display:flex;
  flex-direction:row;
  justify-content:center;
  max-height:200px;
  align-items:center
}
#media-section{
  margin:auto;
  max-width:1200px;
  text-align:center;
  background-color:black;
  color:white;
  min-height:50px;
  display:flex;
  flex-direction:column;
  justify-content:center
}



